import React from "react";

const Follower = ({ follower }) => {
  const {
    avatar_url: image,
    login: name,
    html_url: github_profile,
    id
  } = follower;

  return (
    <article className="card" key={id}>
      <img src={image} alt={name} />
      <h4>{name}</h4>
      <a href={github_profile} className="btn">
        VIEW PROFILE
      </a>
    </article>
  );
};

export default Follower;
